
@import url("https://fonts.googleapis.com/css?family=Coming+Soon");
.Hangman {
  width: 800px;
  color: black;
  text-align: center;
}

.Hangman img {
  border: 10px solid white;
  -webkit-box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
          box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
  width: 25%
}

.Hangman h1 {
  font-size: 4rem;
  font-weight: 100;
  margin-bottom: 0.1em;
  margin-top: 0;
}

.Hangman-word {
  letter-spacing: 1em;
  margin: 0.4em -1em 0.2em 0;
  font-size: 2rem;
}

.Hangman-btns {
  text-align: center;
  display: inline-block;
  width: 350px;
  margin-top: 0;
} 

.Hangman .keyboard {
  border: none;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
  width: 30px;
  margin: 5px 2px 2px 2px;
  letter-spacing: 1px;
  outline: none;
  background: #ffc107;
  color: #fff;
  -webkit-box-shadow: 0 6px #ff9800;
          box-shadow: 0 6px #ff9800;
  border-radius: 5px;
  padding-top: 4px;
}

.Hangman button:hover {
  background-color: #ff9800;
}

.Hangman button:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  -webkit-box-shadow: 0 6px #9e9e9e;
          box-shadow: 0 6px #9e9e9e;
}

.Hangman-count {
  font-size: 1.2rem;
}

.reset-game-btn {
  border: none;
  font-size: 1.3rem;
  font-family: "Coming Soon", cursive;
  width: auto;
  display: block;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
  background: #ffc107;
  -webkit-box-shadow: 0 6px #ff9800;
          box-shadow: 0 6px #ff9800;
  border-radius: 5px;
  color: #fff;
  padding: 5px 10px;
}


#reset:hover {
  background-color: red;
  -webkit-box-shadow: 0 6px crimson;
          box-shadow: 0 6px crimson;

}


